.header {
  position: relative;
  z-index: 20;
}
.header__body {
  position: relative;
  min-height: 100px;
  display: flex;
  align-items: center;
}
.header__logo img {
  width: calc(50px + 50 * ((100vw - 320px) / 1600));
  height: auto;
}
.menu {
  position: absolute;
  left: calc(20px + 10 * ((100vw - 320px) / 1600));
}
.menu__list {
  display: flex;
  gap: calc(10px + 40 * ((100vw - 320px) / 1600));
}
.menu__item a {
  color: #fff;
  font-weight: 700;
  font-size: calc(20px + 20 * ((100vw - 320px) / 1600));
}

