@import "../../styles/variables";
@import "../../styles/mixins";

.sing {
  position: relative;
  z-index: 20;
  // .sing__container
  &__container {
  }

  // .sing__body
  &__body {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 40, 15, 0);
  }

  // .sing__form
  &__form {
  }

  // .sing__button
  &__button {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.form-sing {
  @include adaptive-value('padding', 45, 5, 0);
  background-color: #5E659D;
  border-radius: 10px;
  // .form-sing__body
  &__body {
    display: flex;
    flex-direction: column;
    @include adaptive-value('gap', 40, 15, 0);
  }
}