@import "nullstyle";
@import "fonts";
//@import "icons";
@import "variables";
@import "mixins";


body {
  color: $mainColor;
  font-family: $fontFamily;
  font-size: $fontSize+px;
}

[class*="_container"] {
  //@include adaptive-value('max-width', $maxWidthContainer, 320, 0);
  padding: 0 15px;
  margin: 0 auto;
}

@media (any-hover) {
  ._hover-line {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      top: 100%;
      width: 100%;
      height: 1px;
      background-color: transparent;
      transition: all .3s;
    }
    &:hover {
      &:after {
        background-color: white;
      }
    }
  }
}