@import '../../styles/variables';
@import '../../styles/mixins';


.footer {
  position: relative;
  z-index: 20;
  padding: 20px 0;
  //background-color: $mainBackground;
  // .footer__container
  &__container {
  }

  // .footer__body
  &__body {
    min-height: 50px;
    display: flex;
    align-items: center;
  }

  // .footer__menu
  &__menu {
  }
}

.menu-footer {
  // .menu__list
  &__list {
    display: flex;
    @include adaptive-value('gap', 50, 10, 0);
    flex-wrap: wrap;
  }

  // .menu__item
  &__item {
    & a {
      color: $mainColor;
      font-weight: 700;
      @include adaptive-value('font-size', 35, 16, 0);
    }
  }
}