.sing {
  position: relative;
  z-index: 20;
}
.sing__body {
  display: flex;
  flex-direction: column;
  gap: calc(15px + 25 * ((100vw - 320px) / 1600));
}
.sing__button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-sing {
  padding: calc(5px + 40 * ((100vw - 320px) / 1600));
  background-color: #5E659D;
  border-radius: 10px;
}
.form-sing__body {
  display: flex;
  flex-direction: column;
  gap: calc(15px + 25 * ((100vw - 320px) / 1600));
}

