.form__item {
  display: flex;
  width: calc(280px + 220 * ((100vw - 320px) / 1600));
  align-items: center;
}
.form__title {
  flex: 1 1 auto;
}
.form__title span {
  font-weight: 600;
  font-size: calc(20px + 15 * ((100vw - 320px) / 1600));
}
.form__input {
  padding: 4px 5px;
  border: 2px orange solid;
  border-radius: 5px;
  width: calc(160px + 140 * ((100vw - 320px) / 1600));
}

