@import "../../../styles/variables";
@import "../../../styles/mixins";

.button {
  background-color: #5E659D;
  border-radius: 10px;
  @include adaptive-value('min-width', 310, 160, 0);
  @include adaptive-value('min-height', 75, 60, 0);
  transition: min-width .3s;
  @media (any-hover) {
    &:hover {
      @include adaptive-value('min-width', 290, 100, 0);
      .button__title span {
        @include adaptive-value('font-size', 45, 20, 0);
      }
    }
  }
  // .button__title
  &__title {
    & span {
      color: white;
      font-weight: 700;
      @include adaptive-value('font-size', 50, 24, 1);
      transition: font-size .3s;
    }
  }
}