@import "../../styles/variables";
@import "../../styles/mixins";

.form {
  // .form__item
  &__item {
    display: flex;
    @include adaptive-value('width', 500, 280, 0);
    align-items: center;
  }

  // .form__title
  &__title {
    flex: 1 1 auto;
    & span {
      font-weight: 600;
      @include adaptive-value('font-size', 35, 20, 0);
    }
  }

  // .form__input
  &__input {
    padding: 4px 5px;
    border: 2px orange solid;
    border-radius: 5px;
    @include adaptive-value('width', 300, 160, 0);
  }
}