@import '../../styles/variables';
@import '../../styles/mixins';

.header {
  position: relative;
  z-index: 20;
  //background-color: $mainBackground;
  // .header__container
  &__container {
  }

  // .header__body
  &__body {
    position: relative;
    min-height: 100px;
    display: flex;
    align-items: center;
  }

  // .header__logo
  &__logo {
    & img {
      @include adaptive-value('width', 100, 50, 0);
      height: auto;
    }
  }

  // .header__menu
  &__menu {
  }
}

.menu {
  position: absolute;
  @include adaptive-value('left', 30, 20, 0);
  // .menu__list
  &__list {
    display: flex;
    @include adaptive-value('gap', 50, 10, 0);
  }

  // .menu__item
  &__item {
    & a {
      color: $mainColor;
      font-weight: 700;
      @include adaptive-value('font-size', 40, 20, 0);
    }
  }
}