.button {
  background-color: #5E659D;
  border-radius: 10px;
  min-width: calc(160px + 150 * ((100vw - 320px) / 1600));
  min-height: calc(60px + 15 * ((100vw - 320px) / 1600));
  transition: min-width 0.3s;
}
@media (any-hover) {
  .button:hover {
    min-width: calc(100px + 190 * ((100vw - 320px) / 1600));
  }
  .button:hover .button__title span {
    font-size: calc(20px + 25 * ((100vw - 320px) / 1600));
  }
}
.button__title span {
  color: white;
  font-weight: 700;
  font-size: 50px;
  transition: font-size 0.3s;
}
@media (max-width: 1630px) {
  .button__title span {
    font-size: calc(24px + 26 * ((100vw - 320px) / 1310));
  }
}

