.app {
  margin: 0 auto;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #DBAC74;
}

.image-app {
  position: absolute;
  right: 0;
  bottom: 0;
}
.image-app__page1 {
  width: calc(320px + 330 * ((100vw - 320px) / 1600));
  height: calc(570px + 430 * ((100vw - 320px) / 1600));
}
.image-app__page2 {
  width: calc(320px + 530 * ((100vw - 320px) / 1600));
  height: calc(570px + 430 * ((100vw - 320px) / 1600));
}

.content {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
}

