.main {
  position: relative;
  width: 100%;
  z-index: 20;
}
.main__body {
  display: flex;
  align-items: center;
  position: relative;
}
.info-main {
  min-width: 290px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: calc(15px + 10 * ((100vw - 320px) / 1600));
}
.info-main__title {
  align-self: start;
}
.info-main__title span {
  font-weight: 700;
  font-size: calc(40px + 90 * ((100vw - 320px) / 1600));
}
.info-main__text p {
  font-size: calc(20px + 25 * ((100vw - 320px) / 1600));
}

