@import "../../styles/variables";
@import "../../styles/mixins";

.main {
  position: relative;
  width: 100%;
  z-index: 20;
  // .main__container
  &__container {
  }

  // .main__body
  &__body {
    display: flex;
    align-items: center;
    position: relative;
  }

  // .main__info
  &__info {
  }
}

.info-main {
  min-width: 290px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @include adaptive-value('gap', 25, 15, 0);
  // .info-main__title
  &__title {
    align-self: start;
    & span {
      font-weight: 700;
      @include adaptive-value('font-size', 130, 40, 0);
    }
  }

  // .info-main__text
  &__text {
    & p {
      @include adaptive-value('font-size', 45, 20, 0);
    }
  }
}

