.footer {
  position: relative;
  z-index: 20;
  padding: 20px 0;
}
.footer__body {
  min-height: 50px;
  display: flex;
  align-items: center;
}
.menu-footer__list {
  display: flex;
  gap: calc(10px + 40 * ((100vw - 320px) / 1600));
  flex-wrap: wrap;
}
.menu-footer__item a {
  color: #fff;
  font-weight: 700;
  font-size: calc(16px + 19 * ((100vw - 320px) / 1600));
}

