.feature {
  width: 100%;
}
.feature__body {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767.98px) {
  .feature__body {
    flex-direction: column;
    align-items: center;
  }
}
.feature__image img {
  width: calc(290px + 210 * ((100vw - 320px) / 1600));
  height: auto;
}

.info-feature {
  min-width: 290px;
  max-width: 1700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
  gap: calc(20px + 67 * ((100vw - 320px) / 1600));
}
.info-feature__title {
  align-self: start;
}
.info-feature__title span {
  font-weight: 700;
  font-size: calc(40px + 70 * ((100vw - 320px) / 1600));
}
.info-feature__text p {
  font-size: calc(16px + 8 * ((100vw - 320px) / 1600));
}

.button-feature {
  display: flex;
  flex-wrap: wrap;
  gap: calc(20px + 10 * ((100vw - 320px) / 1600));
}
.button-feature__item {
  min-width: calc(160px + 150 * ((100vw - 320px) / 1600));
  display: flex;
  align-items: center;
  justify-content: center;
}

