@import "../../styles/variables";
@import "../../styles/mixins";

.app {
  margin: 0 auto;
  //min-height: 100vh;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: $mainBackground;
  //max-height: 1080px;
}

.image-app {
  position: absolute;
  right: 0;
  bottom: 0;
  &__page1  {
    @include adaptive-value('width', 650, 320, 0);
    @include adaptive-value('height', 1000, 570, 0);
  }
  &__page2 {
    @include adaptive-value('width', 850, 320, 0);
    @include adaptive-value('height', 1000, 570, 0);
  }
}

.content {
  //background-color: $mainBackground;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
}