@import "../../styles/mixins";
@import "../../styles/variables";

.feature {
  width: 100%;
  // .feature__container
  &__container {
  }

  // .feature__body
  &__body {
    display: flex;
    justify-content: space-between;
    @media (max-width: $md3+px) {
      flex-direction: column;
      align-items: center;
    }
  }

  // .feature__info
  &__info {
  }

  // .feature__image
  &__image {
    & img {
      @include adaptive-value('width', 500, 290, 0);
      height: auto;
    }
  }
}

.info-feature {
  min-width: 290px;
  max-width: 1700px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
  @include adaptive-value('gap', 87, 20, 0);
  // .info-feature__title
  &__title {
    align-self: start;
    & span {
      font-weight: 700;
      @include adaptive-value('font-size', 110, 40, 0);
    }
  }

  // .info-feature__buttons
  &__button {
  }

  // .info-feature__text
  &__text {
    & p {
      @include adaptive-value('font-size', 24, 16, 0);
    }
  }
}

.button-feature {
  display: flex;
  flex-wrap: wrap;
  @include adaptive-value('gap', 30, 20, 0);
  // .button-feature__item
  &__item {
    @include adaptive-value('min-width', 310, 160, 0);
    display: flex;
    align-items: center;
    justify-content: center;
  }
}